.canvasContainer{
display: flex;
justify-content: center;
align-items: center;
/* background-color: lime; */
width: 600px;
position: relative;
margin-top: 300px;
}
.canvasContainer img{
user-select: none;
pointer-events: none;
}
.canvasContainer .canvas-container{
    position:absolute !important ;
    padding: 5px;
    margin-left: 25px;
}
.canvasContainer .canvas-container:last-child{
    border: 1px solid black;
}