.root .dropzone{
    border: none;
    width: 200px !important;
    height: 10px !important;
}

.dropzone-instructions--main{
    display: none;
}

.dropzone-instructions--sub{
    visibility: hidden;
    position: relative;
}
.dropzone-instructions--sub:nth-child(3):after{
   content: "UPLOAD IMAGE";
   color:#3BCE95; 
   border:2px solid #ddd;
   border-radius:5px;
   width:200px;
   text-align:center;
   height:50px; 
   font-size: 20px;
   padding: 10px 0px;
   visibility: visible;
    top: 0px;
    position: absolute;
}