.pageContainer{
padding: 40px 60px;
display: flex;
justify-content: center;
background-color: white;
max-height: calc(100vh - 63px);
overflow: hidden;
}
.createYourDesign{
font-weight: 400;
margin-bottom: 20px;
}
.dropdown-menu{

}
.dropdown-menu a{
  border: 1px solid black;
  padding: 5px 10px;
}
.dropdown-menu a:hover{
  background-color: #eee;
}
.tabContainer{
  margin-bottom: 20px;
  width: 100%  !important;
  max-width: 600px;
}
.tabContainer div{
  overflow: unset !important;
}
.tabContainer div span{
  background-color: transparent !important;
}
.tabContainer .MuiTab-wrapper{
  font-size: 13px;
}
.tabContainer .MuiTab-textColorInherit{
  opacity: 1;
}

.rightPart{
max-width: 660px;
min-width: 660px;
min-height: 600px;
max-height: 600px;
overflow: scroll;
}
/* Hide scrollbar for Chrome, Safari and Opera */
.rightPart::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.rightPart {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}
.txtPart{
  margin-top: 30px;
}

.imgUploadClass button{
font-size: 15px;
width: 200px;
}
  .imgUploadClass button:hover{
  cursor: pointer;
}
.imgUploadClass{
  width: 300px;
  position: relative;
  min-height: 50px;
}
.imgUploadClass input[type="file"] {
    visibility: hidden;
    width: 100%;
}
.upload-btn{
  position: absolute;
  top: 0px;
}